<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <h2> {{ name +'bbb'}}</h2>
    <input type="text" v-model="age">{{age}}
    <label><input type="checkbox" v-model="check">{{check}}</label>
    <h2>Essential Links</h2>
    <ul>
      <li><router-link :to="{path: '/pageone'}">pageone</router-link></li>
      <li><a href="https://forum.vuejs.org" target="_blank">Forum</a></li>
      <li><a href="https://chat.vuejs.org" target="_blank">Community Chat</a></li>
      <li><a href="https://twitter.com/vuejs" target="_blank">Twitter</a></li>
      <br>
      <li><a href="http://vuejs-templates.github.io/webpack/" target="_blank">Docs for This Template</a></li>
    </ul>
    <h2>Ecosystem</h2>
    <ul>
      <li><a href="http://router.vuejs.org/" target="_blank">vue-router</a></li>
      <li><a href="http://vuex.vuejs.org/" target="_blank">vuex</a></li>
      <li><a href="http://vue-loader.vuejs.org/" target="_blank">vue-loader</a></li>
      <li><a href="https://github.com/vuejs/awesome-vue" target="_blank">awesome-vue</a></li>
    </ul>
    <keep-alive>
      <component v-bind:is="component"></component>
    </keep-alive>
    <button @click="component='page-one'">topageone</button>
    <button @click="component='page-two'">topagetwo</button>
  </div>
</template>

<script>
import pageone from "../page/pageone.vue";
import pagetwo from "../page/pagetwo.vue";
export default {
  name: 'HelloWorld',
  components:{
    'page-one':pageone,
    'page-two':pagetwo
  },
  data () {
    return {
      component:'page-one',
      msg: 'Welcome to Your Vue.js App',
      name:'aaa',
      age:'30',
      check:'ture'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



