<template>
  <div class="pageone">
      pagetwo
      <ul v-for="item in list">
        <h2>{{ item.name }}</h2>
        <p>{{ item.age }}</p>
        <p><a class="btn btn-default" href="#" role="button">{{ item.ss }}</a></p>
      </ul>
  </div>
</template>

<script>
export default {
  // vue option 當中有 props 開發區域
  props: {
    // attribute name: Type
    list: Array
  }
  ,data () {
    return {
    }
  }
};
</script>


