<template>
  <div class="pageone">
      <div @click="changetietle">{{tietle}}</div>
      <h2 v-if="!isdc">攝氏：{{ dc }}°C</h2>
      <h2 v-if="isdc">華氏：{{ df }}°F</h2>
      <span v-if="isdc">攝氏：</span><span v-if="!isdc">華氏：</span>
      <input type="number" v-model="userinput">
      <span v-if="isdc">°C</span><span v-if="!isdc">°F</span>
      <input id="dcdf" type="checkbox" v-model="isdc">
    <label for="dcdf">切換：{{ nownumber }}</label>
    <button @click="clickbtn">outputalert</button>
    <ul v-for="item in list">
      <li>{{item.name}}</li>
      <li>{{item.age}}</li>
      <li>{{item.ss}}</li>
    </ul>
    <button @click="deletelist">delete</button>
  </div>
  
</template>

<script>
export default {
  props:{
    list:Array,
    tietle:String
  },
  data() {
    return {
      userinput: 0,
      isdc: true
      /*,
      list:[
        {name:'jack',age:27,ss:'bb'},
        {name:'jk',age:2,ss:'aa'}
        ]
        */
    };
  },
  
  computed: {
    df() {
      return this.userinput * 9 / 5 + 32;
    },
    dc() {
      return (this.userinput - 32) * 5 / 9;
    },
    nownumber() {
      if (this.isdc == true) {
        return "攝氏";
      } else {
        return "華氏";
      }
    }
  },
  methods: {
    clickbtn(val) {
      if (this.isdc) {
        alert("華氏" + this.df);
      } else {
        alert("攝氏" + this.dc);
      }
    },
    deletelist(){
      this.list.pop();
    },
    changetietle(){
      //this.tietle="change";
      this.$emit('changetietle','change');
    }
  }
};
</script>


