<template>
  <div id="app">
    <nav-header>
      <h1 slot='titles'>titleappvue</h1>
      <p>apptext</p>
      
    </nav-header>
    <img src="./assets/logo.png">
    <router-link :to="{path: '/pageone'}">pageone</router-link>
    <router-link :to="{path: '/pagetwo'}">pagetwo</router-link>
    <router-view v-bind:list="list" v-bind:tietle="tietle" v-on:changetietle="newtietle"/>
    
  </div>
</template>


<script>
import navheader from './components/navheader.vue'
export default {
  name: "app",
  components:{'nav-header':navheader},
  data() {
    return {
      list: [
        { name: "jack", age: 27, ss: "bb" },
        { name: "jk", age: 2, ss: "aa" },
        { name: "jack", age: 27, ss: "bb" },
        { name: "jk", age: 2, ss: "aa" },
        { name: "jack", age: 27, ss: "bb" },
        { name: "jk", age: 2, ss: "aa" }
      ]
      ,
      tietle:"pageone"
    };
  },
  methods:{
    newtietle:function(newtietle){
      this.tietle=newtietle;
    }
  }
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

</style>



